@import url('https://fonts.googleapis.com/css2?family=Edu+AU+VIC+WA+NT+Hand:wght@400..700&display=swap');

:root{
  --mainColor: #fff;
  --mainLightColor: rgba(235,235,235,0.5);
  --mainDimColor: rgb(228,228,228);
}

body{
  margin: 0;
  padding: 0;
  overflow: hidden;
  box-sizing: border-box;
}

.container{
  min-height: 100vh;
  min-width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--mainColor);
  font-family: 'Poppins';   
}

.backgroundVideo{
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  object-fit: cover;
  height: 100vh;
  z-index: -1;
  filter: saturate(2.5);
}

.blackScreen{
  width: 100vw;
  height: 100vh;
  position: absolute;
  pointer-events: none;
  background-color: rgba(17,17,17,0.2);
}

.music-Container{
  width: 350px;
  padding: 35px 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 36px;
  box-shadow: 0 0 20px rgba(26,26,26,0.1),
  0 0 40px rgba(26,26,26,0.1),
  0 0 80px rgba(26,26,26,0.1);
  backdrop-filter: blur(15px);
  font-weight: 600;
}
@media screen and (max-width:450px) {
  .music-Container{
    width: 300px;
  }
}
@media screen and (max-width:400px) {
  .music-Container{
    width: 250px;
  }
}
@media screen and (max-width:350px) {
  .music-Container{
    width: 200px;
  }
}
.musicPlayer{
  margin: 0;
  margin-bottom: 10px;
  color: var(--mainLightColor);
}

.music-Head-Name{
  margin: 0 auto;
  font-size: 1.5rem;
  text-align: center;
}

.music-Artist-Name{
  color: var(--mainDimColor);
  margin: 4px 0;
  font-size: 1.2rem;
  font-weight: 400;
}


#songAvatar{
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin: 18px 0;
  position: relative;
  cursor: pointer;
  animation: animateAvatar .5s linear infinite;
}

@keyframes animateAvatar {
  0%,100%{
      transform: scale(1);
  }
  50%{
      transform: scale(1.03);
  }
}

.musicTimerDiv{
  display: flex;
  justify-content: space-between;
  width: 100%;
  font-weight: 600;
}

.musicProgressBar{
  width: 100%;
  margin-bottom: 16px;
  height: 10px;
  border-radius: 5px;
  outline: none;
  filter: hue-rotate(20deg);
}

.playBtn{
  font-size: 32px;
  margin: 8px 10px;
  color: var(--mainColor);
  cursor: pointer;
  font-weight: 400;
}

.musicControler{
  cursor: pointer;
  font-size: 28px;
  color: var(--mainColor);
}

.changeBackBtn{
  width: 200px;
  height: 15px;
  position: absolute;
  border-radius: 0 0 12px 12px;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  transition: .3s;
  color: transparent;
  font-family: "Edu AU VIC WA NT Hand", cursive;
  cursor: pointer;
  padding: 0 0 3px 0;
  font-weight: 600;
  text-align: center;
  box-shadow: 0 0 20px rgba(26,26,26,0.1),
  0 0 40px rgba(26,26,26,0.1),
  0 0 80px rgba(26,26,26,0.1);
  border-bottom: 1px solid rgb(87, 87, 87);
  border-right: 1px solid rgb(87, 87, 87);
  border-left: 1px solid rgb(87, 87, 87);
  backdrop-filter: blur(15px);
}

.changeBackBtn:hover{
  height: 30px;
  color: #ffffff;
}

.objectFitCover{
  object-fit: cover;
}

.objectFitContain{
  object-fit: contain;
}

/* Subs */

.youtube-Subs{
  position: absolute;
  bottom: 15px;
  left: 15px;
  text-decoration: none;
  color: #fff;
  display: flex;
  align-items: center;
}

.youtube-Subs img{
  height: 24px;
}

.youtube-Subs p{
  font-family: 'Poppins';
  margin: 0;
  padding: 0;
  margin-left: 10px;
  font-size: 18px;
}